//== Components
//
//## Define common padding and border radius sizes and more. Values based on 14px text and 1.428 line-height (~20px to start).
$line-height-large:         $global-lineheight !default; // extra decimals for Win 8.1 Chrome
$line-height-small:         $global-lineheight !default;

$border-radius-base:        $global-radius !default;
$border-radius-large:       $global-radius !default;
$border-radius-small:       $global-radius !default;

//** Global color for active items (e.g., navs or dropdowns).
$component-active-color:    $white !default;
//** Global background color for active items (e.g., navs or dropdowns).
$component-active-bg:       $brand-primary !default;



